/******  GLOBAL CSS VARIABLES  ******/
/***  COLORS  ***/
/***  FONTS  ***/
* {
  font-size: 16px;
  font-family: "freight-text-pro", sans-serif;
  outline: none; }

h1, h2, h3, h4, h5, h6 {
  font-family: "chalet-london-sixty", sans-serif;
  color: #6d6e71;
  line-height: 1.3; }

h1 {
  font-size: 45px; }

h2 {
  font-size: 35px; }

h3 {
  font-size: 28px; }

h4 {
  font-size: 22px; }

h5 {
  font-size: 16px; }

h6 {
  font-size: 12px; }

p {
  font-size: 16px;
  line-height: 1.3;
  font-weight: 900; }

/***  MIXINS  ***/
.inputs-component {
  padding: 20px 0; }
  .inputs-component .input-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 500px;
    margin: auto; }
  .inputs-component input, .inputs-component button {
    height: 38px;
    margin: 10px 0;
    background: #d1d3d4;
    border: none; }
  .inputs-component input {
    font-size: 20px;
    color: #6d6e71;
    padding-left: 9px; }
  .inputs-component ::-webkit-input-placeholder {
    color: #a7a9ac; }
  .inputs-component :-ms-input-placeholder {
    color: #a7a9ac; }
  .inputs-component ::-ms-input-placeholder {
    color: #a7a9ac; }
  .inputs-component ::placeholder {
    color: #a7a9ac; }
  .inputs-component button {
    background-color: #a8ed8a;
    color: #6d6e71;
    font-size: 20px;
    cursor: pointer;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    height: 48px; }
  .inputs-component button:hover {
    background-color: #3cd52e; }



